}

.blockquote, .text-long blockquote {
  font-style: italic;
  border-left: solid 3px #e0e0e0;
  margin-left: 1em;
  padding-left: 1em;
}

.hr, .text-long hr {
  border-style: solid;
  border-width: 1px 0 0;
  color: currentColor;
}

.about-x__content {
  display: flex;
  flex-flow: column nowrap;
}

@media (min-width: 600px) {
  .about-x__content {
    flex-direction: row;
  }
}

.about-x__image {
  margin-bottom: 1rem;
}

@media (min-width: 600px) {
  .about-x__image {
    margin-right: 2rem;
    margin-bottom: 0;
    flex: 0 1 164px;
  }
}

.about-x__text {
  flex: 1 1 50%;
}

.date-bar {
  background-color: #007079;
  color: white;
  display: flex;
  flex-flow: column nowrap;
  font-weight: 600;
  justify-content: space-around;
  min-height: 80px;
  padding: 1rem 1rem;
}

.date-bar--ended {
  background-color: #97272c;
}

.date-tile {
  background-color: white;
  border-radius: 2px;
  border: 1px solid black;
  display: inline-flex;
  min-height: 60px;
  padding: 0.25rem 1rem;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.date-tile--floated {
  position: absolute;
  top: 1.5rem;
  left: 1.5rem;
}

.date-tile__separator {
  padding: 0 0.5rem;
}

.date-tile__month {
  font-size: 0.875rem;
}

.date-tile__day {
  font-weight: 600;
}

.event-card {
  display: flex;
  flex-flow: column nowrap;
}

.event-card__image {
  position: relative;
  color: inherit;
}

.event-card__image img {
  width: 100%;
}

.event-card__no-image {
  background: purple;
  height: 0.5rem;
}

.event-card__icon-wrapper {
  background-color: #daa9e3;
  border-radius: 8px;
  padding-top: 75%;
}

.event-card__icon {
  height: 24px;
  width: 24px;
  fill: currentColor;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: #673165;
}

.event-card__icon--up {
  transform: rotate(180deg);
}

.event-card__icon--right {
  transform: rotate(270deg);
}

.event-card__icon--down {
  transform: rotate(0deg);
}

.event-card__icon--left {
  transform: rotate(90deg);
}

.event-card__icon--xlarge {
  height: 68px;
  width: 68px;
}

.event-card__icon--large {
  height: 36px;
  width: 36px;
}

.event-card__icon--small {
  height: 18px;
  width: 18px;
}

.event-card__content {
  order: 1;
  padding: 1rem;
}

.event-card__heading {
  margin: 0 0 1rem;
}

.event-card__status {
  display: inline-block;
  border: 1px solid #e0e0e0;
  background-color: #fafafa;
  padding: 0 0.5rem;
  margin-bottom: 1rem;
  min-width: 6.5rem;
  text-transform: uppercase;
  color: #97272c;
  text-align: center;
  font-size: 0.75rem;
  letter-spacing: 0.005833333em;
}

.event-card__date {
  margin-bottom: 0.5rem;
  letter-spacing: 0.0275em;
}

.event-card__library {
  margin-bottom: 1rem;
}

.event-card__tags {
  padding-top: 0.5rem;
  border-top: 1px solid #bdbdbd;
}

.location-info {
  margin-bottom: 1rem;
}

.location-info__eyebrow {
  font-family: "HelveticaNeue-Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
  font-size: 0.875rem;
  line-height: 1.428571429em;
  letter-spacing: -0.017142857em;
  font-weight: 700;
  color: #673165;
}

.location-info__heading {
  margin-bottom: 1rem;
}

.location-info__directions {
  display: inline-flex;
  flex-flow: row nowrap;
  color: #0071ce;
  text-decoration: none;
  font-size: 0.875em;
  line-height: 1.21;
  letter-spacing: 0.107142857em;
  margin-bottom: 1rem;
}

.location-info__directions:hover {
  color: #004b98;
  text-decoration: underline;
}

.location-info__directions__icon {
  height: 24px;
  width: 24px;
  fill: currentColor;
  margin-left: 0.25rem;
  height: 1rem;
  width: 1rem;
}

.location-info__directions__icon--up {
  transform: rotate(180deg);
}

.location-info__directions__icon--right {
  transform: rotate(270deg);
}

.location-info__directions__icon--down {
  transform: rotate(0deg);
}

.location-info__directions__icon--left {
  transform: rotate(90deg);
}

.location-info__directions__icon--xlarge {
  height: 68px;
  width: 68px;
}

.location-info__directions__icon--large {
  height: 36px;
  width: 36px;
}

.location-info__directions__icon--small {
  height: 18px;
  width: 18px;
}

.location-info__phone {
  font-size: 0.875rem;
  letter-spacing: 0.107142857em;
}

.location-info__phone-number {
  font-size: 1rem;
  letter-spacing: 0.0275em;
  color: #0071ce;
}

.location-info__accessibility {
  margin-bottom: 1rem;
}

.location-info__accessibility-rating {
  font-family: "HelveticaNeue-Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
  font-size: 0.875rem;
  line-height: 1.428571429em;
  letter-spacing: -0.017142857em;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}

.location-info__accessibility-rating .icon {
  margin-right: 0.5rem;
}

.location-info__accessibility-description {
  letter-spacing: 0.0275em;
}

.address-bar__content {
  padding: 1rem;
  border-right: 1px solid #e0e0e0;
  border-bottom: 1px solid #e0e0e0;
  border-left: 1px solid #e0e0e0;
}

.address-bar__content > *:last-child {
  margin-bottom: 0;
}

.address-bar__amenities {
  margin-bottom: 1rem;
  margin-top: 0;
  padding: 0 0 0 1.05em;
}

.address-bar__amenity {
  letter-spacing: 0.0275em;
}

.address-bar__ctas {
  margin-bottom: 1rem;
}

.address-bar__cta {
  display: block;
}

.address-bar__cta + .address-bar__cta {
  margin-top: 0.5rem;
}

.address-bar__cta--secondary {
  padding: 0.5rem;
  border-radius: 2px;
  cursor: pointer;
  font-size: 0.875rem;
  letter-spacing: 1.5px;
  text-decoration: none;
  text-align: center;
  background-color: white;
  border: 1px solid #e0e0e0;
  color: inherit;
}

.address-bar__cta--secondary:visited {
  color: inherit;
}

.address-bar__cta--secondary:hover {
  background-color: #fafafa;
}

.address-bar__cta--primary {
  padding: 0.5rem;
  border-radius: 2px;
  cursor: pointer;
  font-size: 0.875rem;
  letter-spacing: 1.5px;
  text-decoration: none;
  text-align: center;
  background-color: #0071ce;
  border: none;
  color: white;
}

.address-bar__cta--primary:visited {
  color: white;
}

.address-bar__cta--primary:hover {
  background-color: #0070cd;
}

.address-bar__cta-alternate-text {
  padding: 0.5rem;
}

.address-bar__location-hours {
  margin-bottom: 1rem;
}

.address-bar__hours {
  border-spacing: 0;
}

.address-bar__hours-day {
  text-align: left;
  font-weight: normal;
  font-size: 0.875rem;
  letter-spacing: 0.107142857em;
}

.address-bar__hours-hours {
  letter-spacing: 0.0275em;
}

.event-section__heading-section {
  margin-bottom: 1.5rem;
}

.event-section__heading {
  display: inline;
  margin-right: 0.5rem;
}

.event-section__intro {
  margin-bottom: 1.5rem;
}

.event-section__events {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-flow: column nowrap;
}

@media (min-width: 960px) {
  .event-section__events {
    flex-direction: row;
  }
}

.event-section__event + .event-section__event {
  margin-top: 1rem;
}

@media (min-width: 960px) {
  .event-section__event + .event-section__event {
    margin-top: 0;
    margin-left: 2rem;
  }
}

.showcase-hero__header {
  background-color: #f8f8f7;
}

@media (min-width: 600px) {
  .showcase-hero__header {
    display: flex;
    flex-flow: row nowrap;
  }
  .showcase-hero__header > *:first-child {
    flex: 1 1 auto;
  }
  .showcase-hero__header > *:last-child {
    flex: 0 0 40%;
    margin-left: auto;
  }
}

.showcase-hero__heading {
  padding: 1rem;
  margin-bottom: 0;
}

@media (min-width: 600px) {
  .showcase-hero__heading {
    padding: 1.5rem;
  }
}

.showcase-hero__image img {
  width: 100%;
}

.showcase-hero__details {
  background-color: #efedeb;
}

@media (min-width: 600px) {
  .showcase-hero__details {
    display: flex;
    flex-flow: row nowrap;
  }
  .showcase-hero__details > *:first-child {
    flex: 1 1 auto;
  }
  .showcase-hero__details > *:last-child {
    flex: 0 0 40%;
    margin-left: auto;
  }
}

.showcase-hero__location-info {
  padding: 1rem;
}

@media (min-width: 600px) {
  .showcase-hero__location-info {
    padding: 1.5rem;
  }
}

.showcase-hero__location-info a {
  color: #d0343a;
}

.showcase-hero__ctas {
  padding: 1rem;
}

@media (min-width: 600px) {
  .showcase-hero__ctas {
    padding: 1.5rem;
  }
}

.showcase-hero__cta {
  display: block;
}

.showcase-hero__cta--secondary {
  padding: 0.5rem;
  border-radius: 2px;
  cursor: pointer;
  font-size: 0.875rem;
  letter-spacing: 1.5px;
  text-decoration: none;
  text-align: center;
  background-color: white;
  border: 1px solid #e0e0e0;
  color: inherit;
}

.showcase-hero__cta--secondary:visited {
  color: inherit;
}

.showcase-hero__cta--secondary:hover {
  background-color: #fafafa;
}

.slideshow__viewer {
  background-color: #e0e0e0;
}

.slideshow__viewer .slick-track,
.slideshow__viewer .slick-slide {
  display: flex;
  align-items: center;
  justify-content: center;
}

.slideshow__meta {
  padding: 1rem 0;
}

.slick-initialized .slideshow__meta-item {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: flex-end;
}

.slideshow__image-info {
  margin-right: 1rem;
}

.slideshow__image-title {
  font-size: 1.25rem;
  font-weight: bold;
}

.slideshow__image-collection-link {
  color: #0071ce;
  letter-spacing: 0.0275em;
}

.slideshow__counter {
  font-size: 0.75rem;
  letter-spacing: 0.0275em;
}

.slideshow__navigation {
  position: relative;
}

.slideshow__nav {
  z-index: 1;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  height: 24px;
  width: 24px;
  display: flex;
  align-items: center;
  border-radius: 50%;
  border: 1px solid #bdbdbd;
  background: #f5f5f5;
}

.slideshow__nav--prev {
  left: -0.75rem;
}

.slideshow__nav--next {
  right: -0.75rem;
}

.slideshow__nav-icon {
  height: 24px;
  width: 24px;
  fill: currentColor;
  position: relative;
  height: 12px;
  width: 12px;
}

.slideshow__nav-icon--up {
  transform: rotate(180deg);
}

.slideshow__nav-icon--right {
  transform: rotate(270deg);
}

.slideshow__nav-icon--down {
  transform: rotate(0deg);
}

.slideshow__nav-icon--left {
  transform: rotate(90deg);
}

.slideshow__nav-icon--xlarge {
  height: 68px;
  width: 68px;
}

.slideshow__nav-icon--large {
  height: 36px;
  width: 36px;
}

.slideshow__nav-icon--small {
  height: 18px;
  width: 18px;
}

.slideshow__nav-icon--prev {
  left: 0.25rem;
}

.slideshow__nav-icon--next {
  left: 0.35rem;
}

.slideshow__carousel {
  border-top: 1px solid #e0e0e0;
  border-bottom: 1px solid #e0e0e0;
}

.slideshow__carousel .slick-list {
  padding-top: 1rem !important;
  padding-bottom: 1rem !important;
}

.slideshow__carousel .slick-track {
  margin: 1.5rem 0;
}

.slideshow__carousel .slick-current {
  transform: scale(1.25);
  box-shadow: 0 0 0 0.25rem #616161;
}

.slideshow__carousel-item {
  border: 0.5rem solid white;
}

.pl #organisms-slideshow-variable-dimensions .slideshow__navigation .slick-track {
  max-height: 157px;
  overflow: hidden;
}

.pl-place-holder {
  padding: 1rem;
  color: #e0e0e0;
  border: 4px dashed #e0e0e0;
}

.pl-place-holder__content {
  font-size: 3.2rem;
  line-height: 200px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
